.card[class*="Search"]{
    display:none;
}
/*==================================================*/
body > main{
    @include media-breakpoint-up(md) {margin-top:6.25rem;}
}
/*** Sidebar ***/
#infoblock{
    @include transition(all 400ms ease-out);
    [id*="btn"]{
        margin: 0 0 1px 0;
        padding: 1em .5em;
        &:hover,
            &:focus{
            cursor:pointer;
        }
        &[class*="cat"]{
            color:white;
        }

        &.cat-blue{
            background:$blue;
        }
        &.cat-yellow{
            background:$yellow;
        }
        &.cat-pink{
            background:$pink;
        }
        &.cat-green{
            background:$green;
        }
    }
    [id*="collapse"]{
        @include media-breakpoint-up(lg) {display:block;}
    }
    @include media-breakpoint-up(md){
        &[class*="js-is-"]{top:100px !important;}  
    }
    @include media-breakpoint-down(md) {
        margin:1em 0;
        @include border-top-radius(1.2em);
        @include border-bottom-radius(1.2em);
        overflow:hidden;
    }
    @include media-breakpoint-down(xs) {
        &[class*="js-is-"]{
            position:fixed !important;
            top:60px !important;
            left:0;
            right:0;
            background:white;
            z-index:999;
        }
    }
}